.container-only {
  position: relative;

  &:hover {
    z-index: 100;
    .left,
    .right {
      color: white;
    }
    h2 span:last-child {
      opacity: 1;
    }
    .traits {
      opacity: 1;
    }
  }

  h2 {
    font-size: clamp(8px, 1.3vw, 30px);
    margin-top: 5%;
    color: #e5e5e5;
    font-weight: 700;
    margin-bottom: 15px;
    cursor: pointer;
    white-space: nowrap;
    z-index: 1;

    &:hover {
      span {
        transform: translateX(15px);
        opacity: 1;
        &:last-child {
          transform: translateX(20px);
        }
      }
    }

    span {
      display: inline-block;
      transition: transform 0.8s ease-out, opacity 0.8s;
      font-size: clamp(8px, 0.9vw, 15px);
      opacity: 0;

      &:last-child {
        transition: transform 0.3s ease-in;
        transform: translateX(-800%);
        font-size: 0.9em;
      }
    }
  }

  .traits {
    display: flex;
    position: absolute;
    top: 25px;
    right: 10px;
    opacity: 0;

    .trait {
      height: 2px;
      width: 10px;
      margin: 0 1px;
      background-color: #5a5a5a;
      &.trait-active {
        background-color: #cccccc;
      }
    }
  }

  .container-card {
    display: flex;
    transition: transform 0.5s ease-out;

    .card-only {
      display: flex;
      flex-direction: column;
      position: relative;
      z-index: 100;
      overflow: hidden;
      transition: all 0.2s;
      font-size: clamp(8px, 1.3vw, 16px);
      min-width: 16.4%;
      border-radius: 4px;
      cursor: pointer;
      margin-right: 0.25em;
      height: 31vw;
      max-height: 590px;

      &:nth-child(1),
      &:nth-child(7) {
        transform-origin: left;
      }

      &:nth-child(6),
      &:nth-child(12) {
        transform-origin: right;
      }

      &:hover {
        z-index: 101;
        transition: all 0.2s 0.5s;
        transform: scale(1.2);

        .card-content {
          z-index: 200;
          opacity: 1;
          transition: all 0.2s 0.5s;
          background: linear-gradient(transparent, 80%, rgba(0, 0, 0, 0.9));
          color: white;
        }
      }

      ////////////////   background

      .bg-card {
        position: absolute;
        cursor: pointer;
      }

      .card-content {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        opacity: 0;
        height: 100%;
        padding: 0 0.5em 0.5em;
        transition: opacity 0.5s 0.1s, background 0.5s 0.5s;
        font-size: clamp(9px, 1vw, 15px);

        /////////// buttons

        .btns-only {
          display: flex;
          width: 100%;
          margin-bottom: 0.8vw;

          button {
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            background-color: rgba(#202020, 8);
            border-radius: 100%;
            border: 2px solid gray;
            height: 1.5em;
            width: 1.5em;
            font-size: clamp(8px, 2vw, 20px);

            &:hover {
              border: 2px solid white;
            }

            &:first-child {
              margin-right: 0.5em;
              background-color: #fff;
              color: black;
              border: white;
              &:hover {
                background-color: #e6e6e6;
              }
            }

            &:last-child {
              margin-left: auto;
            }
          }
        } ///////////////// info

        .infos-only {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          margin: 5px;

          .age-limit {
            font-size: 0.8em;
            border: 1px solid gray;
            padding: 0.1em 0.3em;
          }

          span {
            margin-right: 5px;
          }
        }

        ///////////////// category

        .category-only {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          width: 64%;

          i {
            margin: 0 8px;
            font-size: 4px;
            color: rgb(107, 107, 107);
          }
        }
      }
    }
  }
}
