footer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #141414;
  color: #808080;
  padding: 0 24%;
  font-size: clamp(10px, 1vw, 13px);

  .footer-logos {
    font-size: 2.3em;
    i {
      margin-right: 0.8em;
      cursor: pointer;
    }
  }

  .footer-links {
    display: flex;
    // justify-content: space-between;
    width: 100%;
    .links {
      display: flex;

      .link {
        display: flex;
        flex-direction: column;
        margin: 10px 6vw 20px 0;

        a {
          padding: 8px 0;
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }

  .footer-code {
    border: 1px solid #808080;
    width: fit-content;
    padding: 7px;
    cursor: pointer;

    &:hover {
      color: white;
    }
  }
  .footer-copyright {
    font-size: clamp(8px, 1vw, 11px);

    margin: 20px 0;
  }
}

// responsive
@media screen and (max-width: 500px) {
  footer {
    padding: 0 5vw;
    justify-content: start;
    .footer-links .links {
      display: block;
      margin: 10px 5px 10px 0;
      .link {
        margin: 0;
        a {
          padding: 2px 2px;
          white-space: nowrap;
        }
      }
    }
  }
}
@media screen and (max-width: 250px) {
  footer .footer-links {
    display: block;
    margin: 5px 0;
    .links {
      margin: 0 5px 0 0;
      .link {
        margin: 0;
        a {
          padding: 2px 2px;
          white-space: nowrap;
        }
      }
    }
  }
}
