.trailer {
  position: relative;
  background: url("../../assets/trailer.jpg") no-repeat center center/ cover;
  min-height: 500px;
  height: 100vh;

  .serie {
    position: absolute;
    width: 50%;
    max-width: 600px;
    bottom: 20vh;
    margin: 0 0 0 3vw;
    color: white;

    .title {
      min-width: 150px;
      width: 50vw;
      max-width: 400px;
    }
    .synopsis {
      font-size: clamp(8px, 2.5vw, 22px);
      text-shadow: 2px 2px 4px rgb(0, 0, 0);
      margin: 0.7em 0;
    }

    .buttons {
      white-space: nowrap;
      width: 100%;

      .btn {
        width: fit-content;
        font-size: clamp(7px, 2vw, 23px);
        padding: 0.7vw 1vw;
        min-width: fit-content;
        font-weight: bold;
        border-radius: 4px;

        span {
          margin: 0 1vw;
        }
      }
      .btn-play {
        padding: 0.7vw 0.8vw 0.7vw 1.8vw;
        &:hover {
          background-color: #cecece;
        }
        margin-right: 10px;
      }
      .btn-info {
        background-color: rgba(109, 109, 110, 0.8);
        color: white;

        &:hover {
          background-color: rgba(68, 68, 68, 0.8);
        }
      }
    }
  }
  // right
  .age-replay {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    position: absolute;
    right: 0;
    bottom: 30%;
    min-width: 10%;
    color: white;

    button {
      background-color: transparent;
      border: 1px solid rgba(255, 255, 255, 0.7);
      color: white;
      border-radius: 100%;
      padding: 0.45em;
      font-size: clamp(0.5em, 3vw, 1.3em);
      &:hover {
        background-color: rgba(51, 51, 51, 0.6);
      }
    }
    .age {
      margin-left: 1em;
      border-left: 3px solid white;
      background-color: rgba(51, 51, 51, 0.6);
      padding: 0.4em 3em 0.4em 0.4em;
      font-size: clamp(0.5em, 3vw, 1.4em);
    }
  }
}

// responsive
@media screen and (max-width: 850px) {
  .trailer {
    height: 80vh;
  }
}
@media screen and (max-width: 500px) {
  .trailer {
    height: 20vh;
  }
}
