nav {
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  z-index: 201;
  height: 68px;
  padding: 2em 3vw;
  color: white;
  background: linear-gradient(rgba(0, 0, 0, 0.9), 65%, transparent);
  text-shadow: 0 0 10px black;
  transition: background 0.3s;

  .options,
  .categories {
    display: flex;
    justify-content: space-between;
    align-items: center;

    a {
      transition: color 0.4s;
      font-size: clamp(12px, 1vw, 14px);
      margin-right: 1em;
      white-space: nowrap;
    }
    // left
    .logo {
      min-width: 40px;
      width: 20%;
      max-width: 92px;
      margin-right: 26px;
      flex-shrink: 0;
      filter: drop-shadow(0 0 10px #000000);
      cursor: pointer;
    }
  }
  .categories {
    font-size: 14px;
    max-width: 580px;
    a:hover {
      color: #b3b3b3;
    }
  }

  .options {
    position: relative;
    width: fit-content;
    font-size: 14px;

    a {
      z-index: 10;
    }
    form {
      display: flex;
      position: absolute;
      align-items: center;
      left: -40px;

      &.active-input {
        label {
          transform: translateX(-250px);
          z-index: 10;
          transform-origin: right;
        }
        input {
          visibility: visible;
          width: 280px;
          transform: translateX(-280px);
          transition: all 0.4s;
        }
      }
      input {
        z-index: -10;
        transform: translateX(-35px);
        transition: width 0.1s;
        visibility: hidden;
        width: 0;
        height: 30px;
        background-color: rgba(0, 0, 0, 0.8);
        outline: 1px solid white;
        border: none;
        padding: 2px 0 2px 50px;
        color: white;
      }
      label {
        transition: all 0.4s;
      }
    }

    .options-links {
      margin-right: 1em;
    }

    .notification {
      position: relative;

      &:hover .notif-active {
        opacity: 1;
        display: block;
        cursor: pointer;
        li:hover {
          background-color: black;
          color: white;
          .description {
            color: white;
          }
        }
      }

      // bell
      .fa-bell {
        font-size: clamp(18px, 2vw, 23px);
        padding: 20px 10px;
        margin-right: 1vmax;
      }

      .notif-active {
        display: none;
        opacity: 0;
        position: absolute;
        width: 65vw;
        max-width: 430px;
        height: auto;
        background-color: rgba(0, 0, 0, 0.9);
        transform: translate(-92%, 6px);
        border-top: 2px solid white;

        .fa-sort-up {
          position: absolute;
          top: -40px;
          right: 5px;
          font-size: 30px;
          color: white;
          padding: 25px 0;
        }
        li {
          height: 90px;
          border: 1px solid rgb(119, 119, 119);
          border-top: none;
          display: flex;
          align-items: center;
          padding: 1.5vw;
          color: #b3b3b3;

          img {
            min-width: 75px;
            width: 10vw;
            max-width: 100px;
            border-radius: 5px;
          }
          .description {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding-left: 20px;
            font-size: clamp(10px, 2vw, 15px);
            color: gainsboro;

            .time {
              color: rgb(119, 119, 119);
            }
          }
        }
      }
    }

    .fa-search {
      font-size: clamp(15px, 1vw, 20px);
    }

    .fas,
    .profil {
      cursor: pointer;
      padding: 15px 0;
    }

    .profil {
      display: flex;
      align-items: center;
      position: relative;
      flex-shrink: 0;

      &:hover {
        i {
          transform: rotate(-180deg);
        }
        .menu-profil {
          opacity: 1;
          display: block;
        }
      }
      .menu-profil {
        display: none;
        opacity: 0;
        position: absolute;
        transform: translate(-69%, 135px);
        background-color: rgba(0, 0, 0, 0.9);
        width: 160px;
        a:hover {
          cursor: pointer;
          text-decoration: underline;
        }

        .accounts {
          display: flex;
          flex-direction: column;
          padding: 5px;
          border-bottom: 1px solid rgb(119, 119, 119);

          a {
            height: 50px;
            display: flex;
            align-items: center;
            padding: 5px 10px;
          }
        }
        .settings {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          padding: 5px;
          a {
            padding: 5px;
          }
        }
      }

      img {
        flex-grow: 0;
        border-radius: 5px;
        margin-right: 10px;
      }

      i {
        transform: translateY(-10%);
        font-size: inherit;
        transition: transform 0.4s;
      }
    }
  }
}

// responsive

.responsive-nav {
  display: none;
  font-size: 12px;
  cursor: pointer;
  padding: 25px 0;
  .fa-sort-up {
    display: none;
  }
  .responsive-links {
    display: none;
    opacity: 0;
    transition: all 1s 2s;
    width: 250px;
  }
}

@media screen and (max-width: 1240px) {
  nav {
    .options {
      form {
        left: -20px;
        &.active-input {
          input {
            width: 185px;
            transform: translateX(-185px);
          }

          label {
            transform: translateX(-165px);
          }
        }
        input {
          height: 22px;
          padding: 2px 0 2px 25px;
        }
      }

      .options-links {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 850px) {
  nav {
    .categories {
      justify-content: flex-start;

      .logo {
        margin-right: 10px;
      }
      .responsive-nav {
        display: block;
        position: relative;

        &:hover {
          .fa-sort-up {
            display: block;
            position: absolute;
            top: 52px;
            left: 50px;
            font-size: 20px;
          }
        }
        &:hover .responsive-links {
          display: flex;
          flex-direction: column;
          align-items: center;
          position: absolute;
          top: 60px;
          left: -68px;
          border-top: 2px solid white;
          background-color: rgba(0, 0, 0, 0.9);
          opacity: 1;
          a {
            font-size: 13px;
            padding: 20px;
            height: 60px;
            width: 100%;
            text-align: center;
            color: #b3b3b3;
            transition: all 0.3s;
            &:hover {
              color: white;
              background-color: rgba(141, 141, 141, 0.1);
            }
          }
        }
      }
      .nav-links {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 430px) {
  nav {
    .form {
      display: none;
    }
    .options .notification .notif-active {
      transform: translate(-88%, 6px);
      li {
        height: 70px;
      }
    }
  }
}
