*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Helvetica, sans-serif;
}

img {
  max-width: 100%;

  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  border: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

main {
  background: linear-gradient(transparent, #141414, 100px, #141414);
  position: relative;
  top: -10vh;
  bottom: 0;
  padding: 2em 3vw 7.9vw;
  margin-bottom: -10vh;
  overflow: hidden;
}
