.container-list {
  position: relative;

  &:hover {
    z-index: 200;
    h2 span:last-child {
      opacity: 1;
    }
    .traits {
      opacity: 1;
    }
  }

  h2 {
    color: #e5e5e5;
    font-weight: 700;
    font-size: clamp(8px, 1.3vw, 30px);
    margin-bottom: 15px;
    cursor: pointer;
    white-space: nowrap;

    &:hover {
      span {
        transform: translateX(15px);
        opacity: 1;
        &:last-child {
          transform: translateX(20px);
        }
      }
    }
    span {
      display: inline-block;
      transition: transform 0.8s ease-out, opacity 0.8s;
      font-size: clamp(8px, 0.9vw, 15px);
      opacity: 0;

      &:last-child {
        transition: transform 0.3s ease-in;
        transform: translateX(-800%);
        font-size: 0.9em;
      }
    }
  }

  .traits {
    display: flex;
    position: absolute;
    top: 25px;
    right: 10px;
    opacity: 0;

    .trait {
      height: 2px;
      width: 10px;
      margin: 0 1px;
      background-color: #5a5a5a;
      &.trait-active {
        background-color: #cccccc;
      }
    }
  }

  .list {
    display: flex;
    height: 7vw;
    max-height: 160px;
    max-width: 1900px;
    transition: transform 0.5s ease-out;
  }
  .items {
    z-index: 100;
    min-width: 16.4%;
    border-radius: 4px;
    background-color: #181818;
    color: #dddddd;
    height: fit-content;
    cursor: pointer;
    margin-right: 4px;
    transform-origin: top -20px;
    transition: all 0.2s, z-index 0.1s;

    img {
      border-radius: 4px;

      &:hover {
        border-radius: 4px 4px 0 0;
        z-index: 300;
      }
    }

    &:hover {
      z-index: 250;
      transform: scale(1.6);
      transition: all 0.2s 0.5s;
      box-shadow: 0 1px 5px black;

      .items-info {
        transition: all 0.2s 0.5s;
        opacity: 1;
        height: 50%;
        padding: 1vw 0.5vw;
      }

      &:nth-child(1),
      &:nth-child(7) {
        -webkit-transform-origin-x: left;
        -webkit-transform-origin-y: top -20px;
      }

      &:nth-child(6),
      &:nth-child(12) {
        -webkit-transform-origin-x: right;
        -webkit-transform-origin-y: top -20px;
      }
    }
    .items-info {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      max-height: 150px;
      opacity: 0;
      height: 0;

      .item {
        z-index: 1000;
        &:nth-child(2) {
          padding: 1em 0;
        }
      }

      .item-buttons {
        display: flex;

        button {
          margin-right: 0.3vw;
          color: white;
          background-color: rgba(#202020, 8);
          border-radius: 100%;
          border: 1px solid gray;
          height: 1.5em;
          width: 1.5em;
          font-size: clamp(4px, 1vw, 18px);
          flex-shrink: 0;

          &:hover {
            border: 1px solid white;
          }

          &:first-child {
            background-color: #fff;
            color: black;
            border: white;
            &:hover {
              background-color: #e6e6e6;
            }
          }

          &:last-child {
            margin-left: auto;
          }
        }
      }

      .item-season {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        font-weight: bold;
        font-size: clamp(4px, 1vw, 10px);

        .recomandation {
          color: rgb(54, 173, 54);
        }
        .age-limit,
        .definition {
          border: 1px solid gray;
          padding: 0.1em 0.3em;
        }
        .definition {
          font-size: clamp(3px, 1vw, 7px);
        }

        span {
          margin-right: 1em;
        }
      }

      .item-category {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        font-size: clamp(3px, 1vw, 10px);

        i {
          margin: 0 1.5em;
          font-size: clamp(2px, 1vw, 3px);
          color: rgb(107, 107, 107);
        }
      }
    }
  }
}
