.btn-slide {
  position: absolute;
  width: 100%;

  button {
    position: absolute;
    background-color: rgba(32, 32, 32, 0.4);
    color: transparent;
    z-index: 2000;
    transition: font-size 0.1s;
    font-size: clamp(5px, 1vw, 30px);

    &.btn-list {
      max-height: 166px;
      height: 8.7vw;
    }
    &.btn-only {
      max-height: 586px;
      height: 31vw;
    }

    &.left {
      left: -3vw;
      min-width: 2.9%;
      border-radius: 0 4px 4px 0;
    }
    &.right {
      right: -3vw;
      min-width: 3.5%;
      border-radius: 4px 0 0 4px;
    }
  }
}

.container-list:hover,
.container-only:hover {
  button {
    color: white;
    &:hover {
      font-size: clamp(7px, 2vw, 33px);
      background-color: rgba(32, 32, 32, 0.7);
    }
  }
}
